const IndexPage = () => {
  const { backgroundImage } = useStaticQuery(
    graphql`
      query {
        backgroundImage: file(relativePath: { eq: "../images/bg.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `
  );

  return(
    <div className="flex flex-col h-screen">
      <NavBar pageTitle="Home"/>
      <BackgroundImage
        Tag="section"
        fluid={backgroundImage.childImageSharp.fluid}
        className="bg-fixed bg-center bg-cover"
      >
        <div className="h-screen flex flex-col justify-center items-center">
          <h1 className={tw`text-3xl font-bold text-white`}>My Page Title</h1>
          <p className={tw`text-lg text-white mt-4`}>My page content here.</p>
        </div>
      </BackgroundImage>
    </div>


  )

  return (
    <Layout pageTitle="Home">
      <p>test</p>
    </Layout>
  )
}

export const Head = () => <title>Home</title>

export default IndexPage
